.loader {
  width: 64px;
  height: 64px;
  position: fixed;
  top: calc(50% - 32px);
  left: calc(50% - 32px);
  box-shadow: inset 0 0 0 50px rgba(170, 170, 170, 0.2), 0 0 0 5000px rgba(170, 170, 170, 0.2); }
  .loader__text {
    text-align: center;
    color: #fff;
    margin-top: 5px;
    font-size: 15px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    left: 50%;
    margin-top: 5px;
    position: absolute;
    width: calc(100vw - 20px);
    max-width: 500px;
    overflow: hidden; }
